const INTEGER_POSITIVE = /^\d+$/;
const INTEGER_FLOAT = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
const PHONE = /^[1][0-9]{10}$/;
const EMAIL = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
const DIGIT = /^\d*?$/;
const KETA11 = /^.{0,11}$/;
const HANKAKU = /^[a-zA-Z0-9]+$/;

const Validator = {
  // 正整数チェック
  integerPositive: (value: any) => INTEGER_POSITIVE.test(value),

  // 二桁小数チェック
  integerFloat: (value: any) => INTEGER_FLOAT.test(value),

  // 携帯電話番号チェック
  phone: (value: any) => PHONE.test(value),

  // メールアドレスチェック
  email: (value: any) => EMAIL.test(value),

  // 11桁チェック
  keta11: (value: any) => KETA11.test(value),

  // 数字チェック
  digit: (value: any) => DIGIT.test(value),

  // 半角英数字チェック
  hankaku: (value: any) => HANKAKU.test(value),
};

export default Validator;
