import createEncryptor from 'redux-persist-transform-encrypt';
import Cookies from 'cookies-js';

import CookieStorage from './cookieStorage';
import ParamsFiles from '../common/paramsFile';

const encryptor: any = createEncryptor({
  secretKey: process.env.REACT_APP_REDUX_PERSIST_ENCRYPTOR_SECRETKEY ? process.env.REACT_APP_REDUX_PERSIST_ENCRYPTOR_SECRETKEY : 'default',
});

const persistConfig = {
  key: 'root',
  storage: new CookieStorage(Cookies, {
    secure: true,
    domain: ParamsFiles.COOKIE_DOMAIN,
    expiration: {
      default: process.env.REACT_APP_REDUX_PERSIST_COOKIE_EXPIRATION,
    },
  }),
  transforms: [encryptor],
  whitelist: ['zenrinSiteStates'],
};

export default persistConfig;
