import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';

import { routerMiddleware } from 'connected-react-router';
import { composeWithDevTools } from 'redux-devtools-extension';
import { History } from 'history';
import { persistReducer, persistStore } from 'redux-persist';

import persistConfig from './utils/persist';

import { IApplicationState, rootReducer } from './redux/store';

/**
 * store配置
 * storeはaction、reducer、thunkの関連する件
 *
 * @export
 * @param {History} history
 * @param {ApplicationState} initialState
 * @returns {Store<ApplicationState>}
 */
export default function configureStore(history: History, initialState: IApplicationState) {
  const composeEnhancers = composeWithDevTools({
    // Specify here name, actionsBlacklist, actionsCreators and other options
  });

  // store作成、reducerを渡す、state初期化
  const store = createStore(
    // rootReducer(history),
    persistReducer(persistConfig, rootReducer(history)),
    initialState,
    composeEnhancers(applyMiddleware(routerMiddleware(history), thunk))
  );

  const persistor: any = persistStore(store);

  // store戻る
  return { persistor, store };
}
