import React from 'react';
import { IError } from '../../../../../redux/models/common.model';
import Tools from '../../../../../utils/commonUtil';
import './ErrorArea.component.css';

interface IProps {
  errors: IError[];
}

type AllProps = IProps;

export default class ErrorArea extends React.Component<AllProps> {
  public render() {
    // 二重送信制御フラグが活性化にする
    Tools.isSubmitFlg = true;
    const { errors } = this.props;
    if (!errors || errors.length <= 0) {
      return null;
    }
    const firstError = errors[0];
    return (
      <div className="content-error">
        <div className="error_area">
          <span className="error_msg_txt">{firstError.message}</span>
        </div>
      </div>
    );
  }
}
