// DCEF1290 reducer
const DCEF1290Reducer: Reducer<IDCEF1290States> = (state = initDCEF1290State, action) => {
  switch (action.type) {
    // DCEF1290
    case DCEF1290ActionTypes.FETCH_API_DCEF1290: {
      return { ...state };
    }
    case DCEF1290ActionTypes.FETCH_API_DCEF1290_SUCCESS: {
      return {
        ...state,
        dCEF1290OutType: action.payload,
      };
    }
    default: {
      return state;
    }
  }
};

export { DCEF1290Reducer };
