import React from 'react';
import { connect } from 'react-redux';
import { Dispatch, bindActionCreators } from 'redux';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import monment from 'moment';

import Validator from '../../../../utils/validator';
import Tools from '../../../../utils/commonUtil';
import { IApplicationState } from '../../../../redux/store';
import DCEF1290Actions from '../../../../redux/modules/DCEF1290.actions';
import DCEF0080Actions from '../../../../redux/modules/DCEF0080.actions';
import CommonActions from '../../../../redux/modules/common.actions';
import { IDCEF1290OutState } from '../../../../redux/models/DCEF1290.model';
import { IDCEF0080OutState } from '../../../../redux/models/DCEF0080.model';
import { IError, IZenrinState } from '../../../../redux/models/common.model';
import ErrorArea from '../../components/layout/errorsArea/ErrorArea.component';
import ParamsFiles from '../../../../common/paramsFile';
import ErrorActions from '../../../../redux/modules/error.actions';
import { ErrorMsgs } from '../../../../common/message';

/**
 * 画面内部用
 *
 * @interface PropsFromState
 */
interface IPropsFromState {
  errors: IError[];
  dCEF1290OutState: IDCEF1290OutState;
  dCEF0080OutState: IDCEF0080OutState;
  zenrinState: IZenrinState;
}

/**
 * 画面用action
 *
 * @interface PropsFromDispatch
 */
interface IPropsFromDispatch {
  getDcef1290List: typeof DCEF1290Actions.fetchDcef1290List;
  getDcef0080List: typeof DCEF0080Actions.fetchDcef0080List;
  dispCurrentErrors: typeof ErrorActions.dispCurrentErrors;
  setPersistor: typeof CommonActions.setPersistor;
}
/**
 * 画面用state
 *
 * @interface StateOwn
 */
interface IStateOwn {
  iriKryMlKboChkbx: boolean;
  mailAdrInput: '';
  mailAdrComfirm: '';
  completeIsDisable: boolean;
  [key: string]: any;
}
// エンテイを統合
type AllProps = IPropsFromState & IPropsFromDispatch & RouteComponentProps;

class DCWBEFRC1060 extends React.Component<AllProps, IStateOwn> {
  public myState = Object(this.props.history.location.state);

  // DCEF1290APIパラメータ初期化
  public initDCEF1290InTypes = {
    root: {
      ktuJho: {
        iriJho: {
          iriTs: monment().format('YYYYMMDDHHmmss'),
          tsyCd: '',
          sinCd: '',
          kkkCdKm: '',
          serIpAdr: '',
          tnmIpAdr: '',
        },
        nisJho: {
          accTok: this.myState.accTok,
          kisId: '12',
          sonId: this.myState.sonId,
          tnmSbtCd: '',
          son2Id: '',
        },
      },
      mesKmk: {
        hkiJho: {
          sniMtoStmId: this.myState.sniMtoStmId,
          sniMtoKinId: this.myState.sniMtoKinId,
          nnsSitMlAdr: this.myState.nnsSitMlAdr,
          nnsSitMlPcKetKbnCd: this.myState.nnsSitMlPcKetKbnCd,
        },
        nmtJho: {
          nmtId: this.myState.nmtId,
          dnpNo: this.myState.dnpNo,
        },
        inoKmk: {
          nisHntCd: this.myState.nisHntCd,
          httKbohttBsyJho: {
            ckeTatSbt: this.myState.F,
            bsyCd: this.myState.DCD,
            uktKnoTs: this.myState.RYMD,
            hokKjnTs: this.myState.KYMD,
          },
        },
      },
    },
  };

  // DCEF0080APIパラメータ初期化
  public initDCEF0080InTypes = {
    root: {
      ktuJho: {
        iriJho: {
          iriTs: monment().format('YYYYMMDDHHmmss'),
          tsyCd: '',
          sinCd: '',
          kkkCdKm: '',
          serIpAdr: '',
          tnmIpAdr: '',
        },
        nisJho: {
          accTok: this.myState.accTok,
          kisId: '12',
          sonId: this.myState.sonId,
          tnmSbtCd: '',
          son2Id: this.myState.ID,
        },
      },
      mesKmk: {
        hkiJho: {
          sniMtoStmId: this.myState.sniMtoStmId,
          sniMtoKinId: this.myState.sniMtoKinId,
        },
        nmtJho: {
          nmtId: this.myState.nmtId,
          dnpNo: this.myState.dnpNo,
        },
        inoKmk: {
          nisHntCd: this.myState.nisHntCd,
          httKbohttBsyJho: {
            httTttCd: this.props.dCEF1290OutState.result.mesKmk.inoKmk.httKbohttBsyJho.httTttCd,
            ckeTatSbt: this.myState.F,
            bsyCd: this.myState.DCD,
            ido: this.props.dCEF1290OutState.result.mesKmk.inoKmk.httKbohttBsyJho.ido,
            ked: this.props.dCEF1290OutState.result.mesKmk.inoKmk.httKbohttBsyJho.ked,
            uktKnoTs: this.myState.RYMD,
            hokKjnTs: this.myState.KYMD,
          },
          iriUsrJho: {
            tel: '',
            nnsSitMlAdr: '',
            iriKryMlKboKbn: '0',
            uknNm: '',
            yno: '',
            uknJys1: '',
            uknJys2: '',
            uknJys3: '',
            uknJys4: '',
          },
        },
      },
    },
  };

  constructor(props: AllProps) {
    super(props);
    this.state = {
      iriKryMlKboChkbx: true,
      mailAdrInput: '',
      mailAdrComfirm: '',
      completeIsDisable: false,
    };
  }

  public componentDidMount() {
    // タイトル設定
    document.title = ParamsFiles.DCWBEFRC1060_TXT.TITLE;
    this.initPostParams();

    const dCEF1290InTypes = this.initDCEF1290InTypes;
    dCEF1290InTypes.root.ktuJho.nisJho.tnmSbtCd = Tools.isSp() ? '3' : '1';
    dCEF1290InTypes.root.ktuJho.nisJho.son2Id = this.myState.son2Id;

    this.props.getDcef1290List(dCEF1290InTypes, (res: IDCEF1290OutState) => {
      let completeIsDisableTem = false;
      if (res.result.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg !== '1') {
        completeIsDisableTem = true;
      }
      this.setState({
        completeIsDisable: completeIsDisableTem,
      });
    });
  }

  // 画面間の引継ぎ項目を再設定
  public initPostParams = () => {
    this.myState.son2Id = this.myState.ID;
    // this.myState.son2Id = this.props.zenrinState.son2Id;
    this.myState.sinCd = this.props.zenrinState.sinCd;
    this.myState.kkkCdKm = this.props.zenrinState.kkkCdKm;
    this.myState.accTok = this.props.zenrinState.accTok;
    this.myState.sonId = this.props.zenrinState.sonId;
    this.myState.sniMtoStmId = this.props.zenrinState.sniMtoStmId;
    this.myState.sniMtoKinId = this.props.zenrinState.sniMtoKinId;
    this.myState.nnsSitMlAdr = this.props.zenrinState.nnsSitMlAdr;
    this.myState.nnsSitMlPcKetKbnCd = this.props.zenrinState.nnsSitMlPcKetKbnCd;
    this.myState.nmtId = this.props.zenrinState.nmtId;
    this.myState.nisHntCd = this.props.zenrinState.nisHntCd;
    this.myState.dnpNo = this.props.zenrinState.dnpNo;
  };

  // この場所に変更する ボタンを押下
  public bsyHenko = () => {
    if (Tools.isSubmit() && this.validatCheck()) {
      const dCEF0080InTypes = this.initDCEF0080InTypes;
      dCEF0080InTypes.root.ktuJho.nisJho.tnmSbtCd = Tools.isSp() ? '3' : '1';
      dCEF0080InTypes.root.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdr = this.state.mailAdrInput
        ? this.state.mailAdrInput
        : this.props.dCEF1290OutState.result.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdr;

      dCEF0080InTypes.root.mesKmk.inoKmk.iriUsrJho.iriKryMlKboKbn = this.state.mailAdrInput === '' ? '1' : '0';
      dCEF0080InTypes.root.mesKmk.inoKmk.iriUsrJho.iriKryMlKboKbn = this.state.iriKryMlKboChkbx ? '1' : '0';

      this.props.getDcef0080List(dCEF0080InTypes, () => {
        const api1290out = this.props.dCEF1290OutState.result;
        const initeParams = {
          sinCd: this.myState.sinCd ? this.myState.sinCd : api1290out.ktuJho.iriJho.sinCd,
          kkkCdKm: this.myState.kkkCdKm ? this.myState.kkkCdKm : api1290out.ktuJho.iriJho.kkkCdKm,
          accTok: this.myState.accTok ? this.myState.accTok : api1290out.ktuJho.nisJho.accTok,
          sonId: this.myState.sonId ? this.myState.sonId : api1290out.ktuJho.nisJho.sonId,
          son2Id: this.myState.son2Id,
          sniMtoStmId: this.myState.sniMtoStmId ? this.myState.sniMtoStmId : api1290out.mesKmk.hkiJho.sniMtoStmId,
          sniMtoKinId: this.myState.sniMtoKinId ? this.myState.sniMtoKinId : api1290out.mesKmk.hkiJho.sniMtoKinId,
          nnsSitMlAdr: this.myState.nnsSitMlAdr ? this.myState.nnsSitMlAdr : api1290out.mesKmk.hkiJho.nnsSitMlAdr,
          nnsSitMlPcKetKbnCd: this.myState.nnsSitMlPcKetKbnCd ? this.myState.nnsSitMlPcKetKbnCd : api1290out.mesKmk.hkiJho.nnsSitMlPcKetKbnCd,
          nmtId: this.myState.nmtId ? this.myState.nmtId : api1290out.mesKmk.nmtJho.nmtId,
          nisHntCd: this.myState.nisHntCd,
          dnpNo: this.myState.dnpNo ? this.myState.dnpNo : api1290out.mesKmk.nmtJho.dnpNo,
          sniMtoGmId: 'DCWBEFRC1060',
        };
        Tools.isSubmitFlg = true;
        this.props.history.push({ pathname: '/ec/complete', state: initeParams });
      });
    }
  };

  // トップへ
  public gotoTop = () => {
    const api1290out = this.props.dCEF1290OutState.result;
    const initeParams = {
      sinCd: this.myState.sinCd ? this.myState.sinCd : api1290out.ktuJho.iriJho.sinCd,
      kkkCdKm: this.myState.kkkCdKm ? this.myState.kkkCdKm : api1290out.ktuJho.iriJho.kkkCdKm,
      accTok: this.myState.accTok ? this.myState.accTok : api1290out.ktuJho.nisJho.accTok,
      sonId: this.myState.sonId ? this.myState.sonId : api1290out.ktuJho.nisJho.sonId,
      son2Id: this.myState.son2Id,
      sniMtoStmId: this.myState.sniMtoStmId ? this.myState.sniMtoStmId : api1290out.mesKmk.hkiJho.sniMtoStmId,
      sniMtoKinId: this.myState.sniMtoKinId ? this.myState.sniMtoKinId : api1290out.mesKmk.hkiJho.sniMtoKinId,
      nnsSitMlAdr: this.myState.nnsSitMlAdr ? this.myState.nnsSitMlAdr : api1290out.mesKmk.hkiJho.nnsSitMlAdr,
      nnsSitMlPcKetKbnCd: this.myState.nnsSitMlPcKetKbnCd ? this.myState.nnsSitMlPcKetKbnCd : api1290out.mesKmk.hkiJho.nnsSitMlPcKetKbnCd,
      nmtId: this.myState.nmtId ? this.myState.nmtId : api1290out.mesKmk.nmtJho.nmtId,
      nisHntCd: this.myState.nisHntCd,
      dnpNo: this.myState.dnpNo ? this.myState.dnpNo : api1290out.mesKmk.nmtJho.dnpNo,
    };
    this.props.history.push({ pathname: '/ec/top', state: initeParams });
  };

  // 変更完了通知先メールアドレス フォーマットチェック
  public mailAdrCheck = (e: any) => {
    const mailAdr = e.target.value;
    if (mailAdr !== '') {
      if (!Validator.email(mailAdr)) {
        if (this.state.completeIsDisable) {
          this.setState({
            completeIsDisable: false,
          });
        }
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_INVALID_ERR]);
      } else {
        this.props.dispCurrentErrors([]);
        this.setState({ mailAdrInput: mailAdr, completeIsDisable: true });
      }
    } else {
      this.props.dispCurrentErrors([]);
      if (this.state.completeIsDisable) {
        this.setState({
          completeIsDisable: false,
        });
      }
    }
  };

  // 画面入力項目チェック
  private validatCheck = () => {
    const dCEF1290InoKmk = this.props.dCEF1290OutState.result.mesKmk.inoKmk;
    // メール入力可能時
    if (dCEF1290InoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg === '1') {
      // 「確認用メールアドレス」入力チェック
      if (this.state.mailAdrInput && this.state.mailAdrComfirm === '') {
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_COMFIRM_ERR2]);
        return false;
      }
      // 「変更受付通知先メールアドレス」入力チェック
      if (this.state.mailAdrInput === '' && this.state.mailAdrComfirm) {
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_INPUT_ERR2]);
        return false;
      }
      // 「確認用メールアドレス」と「変更受付通知先メールアドレス」の一致性チェック
      if (this.state.mailAdrInput !== this.state.mailAdrComfirm) {
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_DIFFRENT_ERR2]);
        return false;
      }
    }
    // メールアドレス必須入力チェック
    if (dCEF1290InoKmk.iriUsrJho.EmlHsuFlg === '1') {
      if (
        (dCEF1290InoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg === '1' && this.state.mailAdrInput === '') ||
        (dCEF1290InoKmk.iriUsrJho.nnsSitMlAdr !== '' && !this.state.iriKryMlKboChkbx)
      ) {
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_MUST_ERR]);
        return false;
      }
    }
    return true;
  };

  public inputChange = (e: any, key: string) => {
    if (key === 'iriKryMlKboChkbx') {
      this.setState((prevState) => ({
        iriKryMlKboChkbx: !prevState.iriKryMlKboChkbx,
      }));
    } else {
      this.setState({ [key]: e.target.value });
    }
  };

  public render() {
    const mesKmkTem = this.props.dCEF1290OutState.result.mesKmk;
    return (
      <main className="content change-place-confirm">
        <ErrorArea errors={this.props.errors} />
        <div className="content-inner">
          <div className="page-header">
            <h1 className="page-title">お受け取り場所の変更</h1>
          </div>
          <form className="change-place-confirm-list">
            <dl>
              <dt>お受け取り店舗</dt>
              <dd>
                {mesKmkTem.inoKmk.httKbohttBsyJho.bsyNm}
                <br />
                {mesKmkTem.inoKmk.httKbohttBsyJho.bsyJys}
              </dd>
              <dt>お受け取り可能日時</dt>
              <dd>{mesKmkTem.inoKmk.nmtMesJho.GmnHyjUktKnoTs}</dd>
              <dt>保管期限</dt>
              <dd>{mesKmkTem.inoKmk.nmtMesJho.GmnHyjUktKgnD}</dd>
              {mesKmkTem.inoKmk.iriUsrJho.nnsSitMlAdr && <dt>完了メールの送付</dt>}
              {mesKmkTem.inoKmk.iriUsrJho.nnsSitMlAdr && (
                <dd className="offset">
                  <div className="drop-service-emai-note">
                    <p>
                      {mesKmkTem.inoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg === '1' ? ParamsFiles.MAIL_CAREFULLY_TEXT1 : ParamsFiles.MAIL_CAREFULLY_TEXT0}
                    </p>
                  </div>
                  <div className="mail-checker">
                    <input
                      type="checkbox"
                      name="send-complete-mail"
                      id="send-complete-mail"
                      onChange={(e) => this.inputChange(e, 'iriKryMlKboChkbx')}
                      defaultChecked={this.state.iriKryMlKboChkbx}
                      disabled={mesKmkTem.inoKmk.iriUsrJho.EmlHsuFlg === '1'}
                    />
                    <label htmlFor="send-complete-mail">{mesKmkTem.inoKmk.iriUsrJho.nnsSitMlAdr}</label>
                  </div>
                </dd>
              )}
              {mesKmkTem.inoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg === '1' && (
                <div className="change-place-block">
                  <div className="change-place-block-title">
                    変更受付通知先メールアドレス<span>（半角英数字）</span>
                  </div>
                  <div className="change-place-block-email-note">
                    <p>
                      {this.props.dCEF1290OutState.result.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg === '0' && ParamsFiles.MAIL_CAREFULLY_TEXT0}
                      {this.props.dCEF1290OutState.result.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg === '1' && ParamsFiles.MAIL_CAREFULLY_TEXT1}
                    </p>
                  </div>
                  <div className="change-place-email-setting">
                    <input type="text" onBlur={(e) => this.mailAdrCheck(e)} onChange={(e) => this.inputChange(e, 'mailAdrInput')} />
                  </div>
                  <div className="change-place-block-title">
                    確認用メールアドレス<span>（半角英数字）</span>
                  </div>
                  <div className="change-place-email-setting">
                    <input type="text" onChange={(e) => this.inputChange(e, 'mailAdrComfirm')} />
                  </div>
                </div>
              )}
            </dl>
            <div className="drop-service-block-note">
              <p>{ParamsFiles.HENKO_IRAI_CAREFULLY_TEXT}</p>
            </div>
            <div className={`change-place-confirm-button${this.state.completeIsDisable ? '' : ' is-disabled'}`}>
              <button type="button" onClick={this.bsyHenko}>
                この場所に変更する
              </button>
            </div>
          </form>
          <div className="return-box">
            <a className="return-button" onClick={this.gotoTop}>
              TOPページへ戻る
            </a>
          </div>
        </div>
      </main>
    );
  }
}

// stateとpropsを関連
const mapStateToProps = ({ dCEF0080States, dCEF1290States, errorsState, zenrinSiteStates }: IApplicationState) => {
  return {
    errors: errorsState.errors,
    dCEF0080OutState: dCEF0080States.dCEF0080OutType,
    dCEF1290OutState: dCEF1290States.dCEF1290OutType,
    zenrinState: zenrinSiteStates.zenrinTypes,
  };
};

// dispatchとコンポーネントpropsを関連
const mapDispatchToProps = (dispatch: Dispatch) => ({
  getDcef0080List: bindActionCreators(DCEF0080Actions.fetchDcef0080List, dispatch),
  getDcef1290List: bindActionCreators(DCEF1290Actions.fetchDcef1290List, dispatch),
  dispCurrentErrors: bindActionCreators(ErrorActions.dispCurrentErrors, dispatch),
  setPersistor: bindActionCreators(CommonActions.setPersistor, dispatch),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DCWBEFRC1060));
