// DCEF0010 reducer
const DCEF0010Reducer: Reducer<IDCEF0010States> = (state = initDCEF0010State, action) => {
  switch (action.type) {
    // APIアクセス
    case DCEF0010ActionTypes.FETCH_API_DCEF0010: {
      return { ...state };
    }
    // APIアクセス成功
    case DCEF0010ActionTypes.FETCH_API_DCEF0010_SUCCESS: {
      return {
        ...state,
        dCEF0010OutType: action.payload,
      };
    }
    default: {
      return state;
    }
  }
};

export { DCEF0010Reducer };
