import React from 'react';
import { connect } from 'react-redux';
import { Dispatch, bindActionCreators } from 'redux';
import { RouteComponentProps, withRouter } from 'react-router';
import monment from 'moment';

import Tools from '../../../../utils/commonUtil';
import { IApplicationState } from '../../../../redux/store';
import DCEF1200Actions from '../../../../redux/modules/DCEF1200.actions';
import { IDCEF1200OutState } from '../../../../redux/models/DCEF1200.model';
import { IError } from '../../../../redux/models/common.model';
import ErrorArea from '../../components/layout/errorsArea/ErrorArea.component';
import ParamsFiles from '../../../../common/paramsFile';

/**
 * 画面内部用
 *
 * @interface PropsFromState
 */
interface IPropsFromState {
  errors: IError[];
  dCEF1200OutState: IDCEF1200OutState;
}

/**
 * 画面用action
 *
 * @interface PropsFromDispatch
 */
interface IPropsFromDispatch {
  getDcef1200List: typeof DCEF1200Actions.fetchDcef1200List;
}

// エンテイを統合
type AllProps = IPropsFromState & IPropsFromDispatch & RouteComponentProps;

class DCWBEFRC1070 extends React.Component<AllProps> {
  public myState = Object(this.props.history.location.state);

  // DCEF0010APIパラメータ初期化
  public initDCEF1200InTypes = {
    root: {
      ktuJho: {
        iriJho: {
          iriTs: monment().format('YYYYMMDDHHmmss'),
          tsyCd: '',
          sinCd: '',
          kkkCdKm: '',
          serIpAdr: '',
          tnmIpAdr: '',
        },
        nisJho: {
          kisId: '12',
          sonId: this.myState.tnmSbtCd,
          tnmSbtCd: '',
          son2Id: this.myState.son2Id,
        },
      },
      mesKmk: {
        nmtJho: {
          nmtId: this.myState.nmtId,
          dnpNo: this.myState.dnpNo,
        },
        inoKmk: {
          nisHntCd: this.myState.nisHntCd,
        },
      },
    },
  };

  public componentDidMount() {
    document.title = ParamsFiles.DCWBEFRC1070_TXT.TITLE;
    const Dcef1200In = this.initDCEF1200InTypes;
    Dcef1200In.root.ktuJho.nisJho.tnmSbtCd = Tools.isSp() ? '3' : '1';
    this.props.getDcef1200List(this.initDCEF1200InTypes);
  }

  // トップへ遷移
  public gotoTop = (e: any) => {
    e.preventDefault();
    const initeParams = {
      sinCd: this.myState.sinCd,
      kkkCdKm: this.myState.kkkCdKm,
      accTok: this.myState.accTok,
      sonId: this.myState.sonId,
      son2Id: this.myState.son2Id,
      sniMtoStmId: this.myState.sniMtoStmId,
      sniMtoKinId: this.myState.sniMtoKinId,
      nnsSitMlAdr: this.myState.nnsSitMlAdr,
      nnsSitMlPcKetKbnCd: this.myState.nnsSitMlPcKetKbnCd,
      nmtId: this.myState.nmtId,
      nisHntCd: this.myState.nisHntCd,
      dnpNo: this.myState.dnpNo,
    };
    this.props.history.push({ pathname: '/ec/top', state: initeParams });
  };

  public render() {
    const inoKmkTem = this.props.dCEF1200OutState.result.mesKmk.inoKmk;
    return (
      <main className="content change-drop-service-complete">
        <ErrorArea errors={this.props.errors} />
        <div className="content-inner">
          <div className="page-header">
            {this.myState.sniMtoGmId === 'DCWBEFRC1030' && <h1 className="page-title">敷地内配達の日付／指定場所の変更</h1>}
            {this.myState.sniMtoGmId === 'DCWBEFRC1060' && <h1 className="page-title">お受取場所の変更</h1>}
          </div>
          {this.myState.sniMtoGmId === 'DCWBEFRC1030' && (
            <div className="drop-service-steps">
              <div className="drop-service-step">1.日付変更</div>
              <div className="drop-service-step">2.指定場所変更</div>
              <div className="drop-service-step is-current">3.完了</div>
            </div>
          )}
          <div className="change-complete-title">変更が完了いたしました</div>
          <div className="order-num">送り状番号：{this.myState.dnpNo}</div>
          <h2 className="delivery-status-title">配達情報</h2>
          <div className="delivery-status">
            <div className="status-item">
              <div className="heading">
                <div className="heading-inner">配達状況</div>
              </div>
              <div className="body">{inoKmkTem.nmtMesJho.httJky}</div>
            </div>
            <div className="status-item">
              <div className="heading">
                <div className="heading-inner">送り主</div>
              </div>
              <div className="body">{inoKmkTem.nmtMesJho.Okn}</div>
            </div>
            <div className="status-item">
              <div className="heading">
                <div className="heading-inner">荷物サイズ</div>
              </div>
              <div className="body">{inoKmkTem.nmtMesJho.GmnHyjNmtSiz}</div>
            </div>

            <div className="status-item">
              <div className="heading">
                <div className="heading-inner">
                  {this.myState.sniMtoGmId === 'DCWBEFRC1030' && 'お届け予定日'}
                  {this.myState.sniMtoGmId === 'DCWBEFRC1060' && 'お受け取り可能日時'}
                </div>
              </div>
              <div className="body">
                {this.myState.sniMtoGmId === 'DCWBEFRC1030' && inoKmkTem.nmtMesJho.GmnHyjOtdTs}
                {this.myState.sniMtoGmId === 'DCWBEFRC1060' && inoKmkTem.nmtMesJho.GmnHyjUktKnoTs}
              </div>
            </div>
            {this.myState.sniMtoGmId === 'DCWBEFRC1060' && (
              <div className="status-item">
                <div className="heading">
                  <div className="heading-inner">保管期限</div>
                </div>
                <div className="body">{inoKmkTem.nmtMesJho.GmnHyjUktKgnD}</div>
              </div>
            )}
            <div className="status-item">
              <div className="heading">
                <div className="heading-inner">お届け予定場所</div>
              </div>
              <div className="body">{inoKmkTem.nmtMesJho.GmnHyjUktBsy}</div>
            </div>
            <div className="status-item">
              <div className="heading">
                <div className="heading-inner">配達完了メール</div>
              </div>
              <div className="body">
                {inoKmkTem.iriUsrJho.iriKryMlKboKbn === '0' ? '送信しない' : '送信する'}
                <br />
                {inoKmkTem.iriUsrJho.nnsSitMlAdr}
              </div>
            </div>
          </div>
          {this.myState.nisHntCd !== '01' && (
            <div className="register-member-box">
              <div className="register-member-lead">{ParamsFiles.DCWBEFRC1070_TXT.CROWS_MEMBERS_ANNAI_TEXT}</div>
              <a href={ParamsFiles.DCWBEFRC1070_TXT.CROWS_MEMBERS_URL} className="register-member-button" target="_blank" rel="noreferrer">
                {ParamsFiles.DCWBEFRC1070_TXT.CROWS_MEMBERS_BTN_TEXT}
              </a>
            </div>
          )}
          <div className="return-box">
            <a href="###" className="return-button" onClick={this.gotoTop}>
              TOPページへ戻る
            </a>
          </div>
        </div>
      </main>
    );
  }
}
// stateとpropsを関連
const mapStateToProps = ({ dCEF1200States, errorsState }: IApplicationState) => {
  return {
    errors: errorsState.errors,
    dCEF1200OutState: dCEF1200States.dCEF1200OutType,
  };
};

// dispatchとコンポーネントpropsを関連
const mapDispatchToProps = (dispatch: Dispatch) => ({
  getDcef1200List: bindActionCreators(DCEF1200Actions.fetchDcef1200List, dispatch),
});
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DCWBEFRC1070));
