import axios, { AxiosInstance, AxiosResponse } from 'axios';
import { ErrorMsgs } from '../../common/message';
import ParamsFiles from '../../common/paramsFile';

const fetch: AxiosInstance = axios.create({
  timeout: ParamsFiles.API_TIMEOUT,
  baseURL: ParamsFiles.API_BASE_URL ? ParamsFiles.API_BASE_URL : '',
  headers: {
    'Content-Type': 'application/json',
  },
});

fetch.interceptors.response.use(
  // 正常にアクセス
  (response: AxiosResponse) => {
    return response;
  },
  // 異常
  (response: AxiosResponse) => {
    return {
      data: {
        errors: [
          {
            field: response.status,
            code: ErrorMsgs.DCEF015FEM.code,
            message: ErrorMsgs.DCEF015FEM.message,
          },
        ],
      },
    };
  }
);

export default fetch;
