import { Reducer } from 'redux';
import { commonActionTypes, IZenrinStates } from '../models/common.model';

// state初期化
export const initZenrin: IZenrinStates = {
  zenrinTypes: {
    sinCd: '',
    kkkCdKm: '',
    accTok: '',
    sonId: '',
    son2Id: 0,
    sniMtoStmId: '',
    sniMtoKinId: '',
    nnsSitMlAdr: '',
    nnsSitMlPcKetKbnCd: '',
    dnpNo: '',
    nmtId: '',
    nisHntCd: '',
  },
};

const CommonReducer: Reducer<IZenrinStates> = (state = initZenrin, action) => {
  switch (action.type) {
    case commonActionTypes.ZENEIN_SET: {
      return {
        ...state,
        zenrinTypes: action.payload,
      };
    }
    case commonActionTypes.ZENEIN_CLEAR: {
      return {
        ...state,
        zenrinTypes: null,
      };
    }
    default: {
      return state;
    }
  }
};

export { CommonReducer };
