              {this.errorsArr10.includes(this.state.code) && this.state.message}
              {this.errorsArr10.includes(this.state.code) && <br />}
              {this.errorsArr10.includes(this.state.code) && <br />}
              {this.errorsArr8.includes(this.state.code) && '[よくあるお問い合わせは'}
              {this.errorsArr8.includes(this.state.code) && <a href={ParamsFiles.OSIRASE_FOOTER_URL1}>こちら</a>}
              {this.errorsArr8.includes(this.state.code) && '］'}
              {this.errorsArr9.includes(this.state.code) && <a onClick={this.gotoTop}>配送状況を確認</a>}
