              {!this.defaultFmt.includes(this.state.code) && this.state.message}
              {!this.defaultFmt.includes(this.state.code) && <br />}
              {!this.defaultFmt.includes(this.state.code) && <br />}
              {!this.defaultFmt.includes(this.state.code) && '[よくあるお問い合わせは'}
              {!this.defaultFmt.includes(this.state.code) && <a href={ParamsFiles.OSIRASE_FOOTER_URL1}>こちら</a>}
