import { Reducer } from 'redux';
import { DCEF0080ActionTypes, IDCEF0080States } from '../models/DCEF0080.model';

// state初期化
export const initDCEF0080State: IDCEF0080States = {
  dCEF0080OutType: {
    result: {
      jkoKka: '',
    },
  },
};

// DCEF0080 reducer
const DCEF0080Reducer: Reducer<IDCEF0080States> = (state = initDCEF0080State, action) => {
  switch (action.type) {
    // DCEF0080
    case DCEF0080ActionTypes.FETCH_API_DCEF0080: {
      return { ...state };
    }
    case DCEF0080ActionTypes.FETCH_API_DCEF0080_SUCCESS: {
      return {
        ...state,
        dCEF0040OutType: action.payload,
      };
    }
    default: {
      return state;
    }
  }
};

export { DCEF0080Reducer };
