import * as React from 'react';
import { Switch, Route, Redirect, withRouter, RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';

import Header from './header/header.component';
import DCWBEFRC1010 from '../../pages/DCWBEFRC1010/DCWBEFRC1010.page';
import DCWBEFRC1020 from '../../pages/DCWBEFRC1020/DCWBEFRC1020.page';
import DCWBEFRC1030 from '../../pages/DCWBEFRC1030/DCWBEFRC1030.page';
import DCWBEFRC1060 from '../../pages/DCWBEFRC1060/DCWBEFRC1060.page';
import DCWBEFRC1070 from '../../pages/DCWBEFRC1070/DCWBEFRC1070.page';
import DCWBEFRC1080 from '../../pages/DCWBEFRC1080/DCWBEFRC1080.page';

// 属性
type AllProps = RouteComponentProps;

/**
 * レイアウト
 *
 * @class MsLayout
 * @extends {React.Component}
 */
class MsLayout extends React.Component<AllProps> {
  // レンダリング
  public render() {
    return (
      <div className="ec-container">
        <Header />
        <Switch>
          <Route path="/ec/link" exact component={DCWBEFRC1010} />
          <Route path="/ec/top" exact component={DCWBEFRC1020} />
          <Route path="/ec/henko" exact component={DCWBEFRC1030} />
          <Route path="/ec/place" exact component={DCWBEFRC1060} />
          <Route path="/ec/complete" exact component={DCWBEFRC1070} />
          <Route path="/ec/osirase" exact component={DCWBEFRC1080} />
          <Route path="/" exact component={DCWBEFRC1080} />
          <Redirect to="/" />
        </Switch>
      </div>
    );
  }
}

// dispatchとコンポーネントのpropsを関連する
const mapDispatchToProps = () => ({});

export default withRouter(connect(mapDispatchToProps)(MsLayout));
