import { Reducer } from 'redux';
import { DCEF1300ActionTypes, IDCEF1300States } from '../models/DCEF1300.model';

// state初期化
export const initDCEF1300State: IDCEF1300States = {
  dCEF1300OutType: {
    result: {
      mesKmk: {
        nmtJho: {
          dnpSbt: '',
        },
      },
    },
  },
};

// DCEF1300 reducer
const DCEF1300Reducer: Reducer<IDCEF1300States> = (state = initDCEF1300State, action) => {
  switch (action.type) {
    // DCEF1300
    case DCEF1300ActionTypes.FETCH_API_DCEF1300: {
      return { ...state };
    }
    case DCEF1300ActionTypes.FETCH_API_DCEF1300_SUCCESS: {
      return {
        ...state,
        dCEF1300OutType: action.payload,
      };
    }
    default: {
      return state;
    }
  }
};

export { DCEF1300Reducer };
