import { combineReducers, Dispatch, Action, AnyAction } from 'redux';
import { connectRouter, RouterState } from 'connected-react-router';
import { History } from 'history';

import { IErrorsState, IZenrinStates } from './models/common.model';

import { IDCEF0010States } from './models/DCEF0010.model';
import { IDCEF0020States } from './models/DCEF0020.model';
import { IDCEF0040States } from './models/DCEF0040.model';
import { IDCEF0060States } from './models/DCEF0060.model';
import { IDCEF1300States } from './models/DCEF1300.model';
import { IDCEF1200States } from './models/DCEF1200.model';
import { IDCEF1180States } from './models/DCEF1180.model';
import { IDCEF1290States } from './models/DCEF1290.model';
import { IDCEF0080States } from './models/DCEF0080.model';

import { ErrorsReducer } from './modules/errors.reducer';
import { DCEF0010Reducer } from './modules/DCEF0010.reducer';
import { DCEF0020Reducer } from './modules/DCEF0020.reducer';
import { DCEF0040Reducer } from './modules/DCEF0040.reducer';
import { DCEF0060Reducer } from './modules/DCEF0060.reducer';
import { DCEF1300Reducer } from './modules/DCEF1300.reducer';
import { DCEF1200Reducer } from './modules/DCEF1200.reducer';
import { DCEF1180Reducer } from './modules/DCEF1180.reducer';
import { DCEF1290Reducer } from './modules/DCEF1290.reducer';
import { DCEF0080Reducer } from './modules/DCEF0080.reducer';
import { CommonReducer } from './modules/common.reducer';

// root state
export interface IApplicationState {
  // ルート
  router: RouterState<any>;

  errorsState: IErrorsState;

  dCEF0010States: IDCEF0010States;
  dCEF0020States: IDCEF0020States;
  dCEF0040States: IDCEF0040States;
  dCEF0060States: IDCEF0060States;
  dCEF1300States: IDCEF1300States;
  dCEF1200States: IDCEF1200States;
  dCEF1180States: IDCEF1180States;
  dCEF1290States: IDCEF1290States;
  dCEF0080States: IDCEF0080States;
  zenrinSiteStates: IZenrinStates;
}

// 属性をreactコンポーネントに追加して、該当属性はconnectメソッドへ渡し
export interface IConnectedReduxProps<A extends Action = AnyAction> {
  dispatch: Dispatch<A>;
}

// Actionを呼び出し時、ReduxはAction名によりって、Reducerを利用して、stateを更新する
export const rootReducer = (history: History<any>) =>
  combineReducers<IApplicationState>({
    // ルート
    router: connectRouter(history),

    errorsState: ErrorsReducer,

    dCEF0010States: DCEF0010Reducer,
    dCEF0020States: DCEF0020Reducer,
    dCEF0040States: DCEF0040Reducer,
    dCEF0060States: DCEF0060Reducer,
    dCEF1300States: DCEF1300Reducer,
    dCEF1200States: DCEF1200Reducer,
    dCEF1180States: DCEF1180Reducer,
    dCEF1290States: DCEF1290Reducer,
    dCEF0080States: DCEF0080Reducer,
    zenrinSiteStates: CommonReducer,
  });
