import { Dispatch } from 'redux';
import { push } from 'connected-react-router';
import { commonActionTypes } from '../models/common.model';
import { ErrorMsgs } from '../../common/message';

const CommonActions = {
  setPersistor(params: any) {
    return (dispatch: Dispatch) => {
      dispatch({ type: commonActionTypes.ZENEIN_SET, payload: params });
    };
  },
  clearPersistor() {
    return (dispatch: Dispatch) => {
      dispatch({ type: commonActionTypes.ZENEIN_CLEAR });
    };
  },
  extraParamsMustNg() {
    return (dispatch: Dispatch) => {
      dispatch(
        push({
          pathname: '/ec/osirase',
          state: {
            code: ErrorMsgs.DCEF101FEM.code,
            message: ErrorMsgs.DCEF101FEM.message,
          },
        })
      );
    };
  },
};
export default CommonActions;
