import { Reducer } from 'redux';
import { DCEF0060ActionTypes, IDCEF0060States } from '../models/DCEF0060.model';

// state初期化
export const initDCEF0060State: IDCEF0060States = {
  dCEF0060OutType: {
    result: {
      jkoKka: '0',
    },
  },
};

// DCEF0060 reducer
const DCEF0060Reducer: Reducer<IDCEF0060States> = (state = initDCEF0060State, action) => {
  switch (action.type) {
    // DCEF0060
    case DCEF0060ActionTypes.FETCH_API_DCEF0060: {
      return { ...state };
    }
    case DCEF0060ActionTypes.FETCH_API_DCEF0060_SUCCESS: {
      return {
        ...state,
        dCEF0040OutType: action.payload,
      };
    }
    default: {
      return state;
    }
  }
};

export { DCEF0060Reducer };
