import { Reducer } from 'redux';
import { commonActionTypes, IErrorsState } from '../models/common.model';

// state初期化
export const initErrors: IErrorsState = {
  errors: [],
};

// api error reducer
const ErrorsReducer: Reducer<IErrorsState> = (state = initErrors, action) => {
  switch (action.type) {
    case commonActionTypes.API_ERROR: {
      return {
        ...state,
        errors: action.payload,
      };
    }
    case commonActionTypes.VALIDATOR_ERROR: {
      return {
        errors: action.payload,
      };
    }
    case commonActionTypes.ERROR_CLEAR: {
      return {
        errors: [],
      };
    }
    default: {
      return state;
    }
  }
};

export { ErrorsReducer };
