import React from 'react';
import { connect } from 'react-redux';
import { Dispatch, bindActionCreators } from 'redux';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import monment from 'moment';

import { IApplicationState } from '../../../../redux/store';
import Tools from '../../../../utils/commonUtil';
import ErrorArea from '../../components/layout/errorsArea/ErrorArea.component';
import DCEF0020Actions from '../../../../redux/modules/DCEF0020.actions';
import DCEF0060Actions from '../../../../redux/modules/DCEF0060.actions';
import ErrorActions from '../../../../redux/modules/error.actions';
import { IDCEF0020OutState, IHttKnoScnSteBsyJhoKno } from '../../../../redux/models/DCEF0020.model';
import { IDCEF0060OutState } from '../../../../redux/models/DCEF0060.model';
import { IError } from '../../../../redux/models/common.model';
import ParamsFiles from '../../../../common/paramsFile';
import Validator from '../../../../utils/validator';
import { ErrorMsgs } from '../../../../common/message';
/**
 * 画面利用する属性
 *
 * @interface PropsFromState
 */
interface IPropsFromState {
  errors: IError[];
  dCEF0020OutState: IDCEF0020OutState;
  dCEF0060OutState: IDCEF0060OutState;
}

/**
 * 画面利用するaction
 *
 * @interface PropsFromDispatch
 */
interface IPropsFromDispatch {
  getDcef0020List: typeof DCEF0020Actions.fetchDcef0020List;
  getDcef0060List: typeof DCEF0060Actions.fetchDcef0060List;
  dispCurrentErrors: typeof ErrorActions.dispCurrentErrors;
}

/**
 * 画面用state
 *
 * @interface StateOwn
 */
interface IStateOwn {
  dispKbn: number;
  httDate: string;
  heightAuto: boolean;
  httKnoScnSteBsyJhoKno: IHttKnoScnSteBsyJhoKno;
  mailAdrInput: string;
  mailAdrComfirm: string;
  iriKryMlKboKbn: string;
  iriKryMlKboChkbx: boolean;
  completeIsDisable: boolean;
  [key: string]: any;
}

type AllProps = IPropsFromState & IPropsFromDispatch & RouteComponentProps;

class DCWBEFRC1030 extends React.Component<AllProps, IStateOwn> {
  public myState = Object(this.props.history.location.state);

  public ktuJho = {
    iriJho: {
      iriTs: monment().format('YYYYMMDDHHmmss'),
      tsyCd: '',
      sinCd: '',
      kkkCdKm: '',
      serIpAdr: '', // 課題No.38確認済み、空にする
      tnmIpAdr: '', // 課題No.38確認済み、空にする
    },
    nisJho: {
      kisId: '12',
      sonId: this.myState.sonId,
      tnmSbtCd: '',
      son2Id: this.myState.son2Id,
    },
  };

  // DCEF00620APIパラメータ初期化
  public initDCEF0060InTypes = {
    root: {
      ktuJho: {
        iriJho: {
          iriTs: monment().format('YYYYMMDDHHmmss'),
          tsyCd: '',
          sinCd: '',
          kkkCdKm: '',
          serIpAdr: '',
          tnmIpAdr: '',
        },
        nisJho: {
          kisId: '12',
          sonId: this.myState.sonId,
          tnmSbtCd: '',
          son2Id: this.myState.son2Id,
        },
      },
      mesKmk: {
        hkiJho: {
          sniMtoStmId: this.myState.sniMtoStmId,
          sniMtoKinId: this.myState.sniMtoKinId,
        },
        nmtJho: {
          nmtId: this.myState.nmtId,
          dnpNo: this.myState.dnpNo,
        },
        inoKmk: {
          nisHntCd: this.myState.nisHntCd,
          iriUsrJho: {
            tel: '',
            nnsSitMlAdr: '',
            nnsSitMlPcKetKbnCd: '',
            iriKryMlKboKbn: '',
          },
          nmtMesJho: {
            scnHttKboHdk: '',
            scnHttKboBsyCd: '',
          },
        },
      },
    },
  };

  // DCEF0020APIパラメータ初期化
  public initDCEF0020InTypes = {
    root: {
      ktuJho: this.ktuJho,
      mesKmk: {
        hkiJho: {
          nnsSitMlAdr: this.myState.nnsSitMlAdr,
          nnsSitMlPcKetKbnCd: this.myState.nnsSitMlPcKetKbnCd,
        },
        nmtJho: {
          dnpNo: this.myState.dnpNo,
          nmtId: this.myState.nmtId,
        },
        inoKmk: {
          nisHntCd: this.myState.nisHntCd,
        },
      },
    },
  };

  constructor(props: AllProps) {
    super(props);
    this.state = {
      dispKbn: 0,
      httDate: '',
      heightAuto: false,
      httKnoScnSteBsyJhoKno: {
        scnHttbsyCd: '',
        scnHttbsyNm: '',
        scnHttbsyHenMaeStkFlg: '',
      },
      mailAdrInput: '',
      mailAdrComfirm: '',
      iriKryMlKboKbn: '0',
      iriKryMlKboChkbx: true,
      completeIsDisable: false,
    };
  }

  public componentDidMount() {
    // タイトル設定
    document.title = ParamsFiles.DCWBEFRC1030_TXT.TITLE;
    const dCEF0020InTypes = this.initDCEF0020InTypes;
    dCEF0020InTypes.root.ktuJho.nisJho.tnmSbtCd = Tools.isSp() ? '3' : '1';

    this.props.getDcef0020List(dCEF0020InTypes, (res: IDCEF0020OutState) => {
      const { inoKmk } = res.root.mesKmk;
      let httDateTem = '';
      inoKmk.httKboScnHttHdkJhoKnoLst.map((item) => {
        if (item.scnHttHdkHenMaeStkFlg === '1') {
          httDateTem = item.scnHttHdk;
        }
        return '';
      });

      let completeIsDisableTem = false;
      if (inoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg !== '1') {
        completeIsDisableTem = true;
      }
      this.setState({
        httDate: httDateTem,
        httKnoScnSteBsyJhoKno: Object(inoKmk.httKnoScnSteBsyJhoKnoLst.find((item) => item.scnHttbsyHenMaeStkFlg === '1')),
        iriKryMlKboKbn: inoKmk.iriUsrJho.iriKryMlKboKbn,
        completeIsDisable: completeIsDisableTem,
      });
    });
  }

  // 日付変更/場所変更切り替え
  public dispChange = (kbn: number) => {
    this.props.dispCurrentErrors([]);
    this.setState({
      heightAuto: false,
      dispKbn: kbn,
    });
  };

  // トップへ遷移
  public gotoTop = () => {
    const initeParams = {
      sinCd: this.myState.sinCd,
      kkkCdKm: this.myState.kkkCdKm,
      accTok: this.myState.accTok,
      sonId: this.myState.sonId,
      son2Id: this.myState.son2Id,
      sniMtoStmId: this.myState.sniMtoStmId,
      sniMtoKinId: this.myState.sniMtoKinId,
      nnsSitMlAdr: this.myState.nnsSitMlAdr,
      nnsSitMlPcKetKbnCd: this.myState.nnsSitMlPcKetKbnCd,
      nmtId: this.myState.nmtId,
      nisHntCd: this.myState.nisHntCd,
      dnpNo: this.myState.dnpNo,
    };
    this.props.history.push({ pathname: '/ec/top', state: initeParams });
  };

  // 日付選択
  public changeDate = (item: any) => {
    this.setState({
      httDate: item.scnHttHdk,
    });
  };

  public inputChange = (e: any, key: string) => {
    if (key === 'iriKryMlKboChkbx') {
      this.setState((prevState) => ({
        iriKryMlKboChkbx: !prevState.iriKryMlKboChkbx,
      }));
    } else {
      this.setState({ [key]: e.target.value });
    }
  };

  public closePulldown = () => {
    this.setState({
      heightAuto: false,
    });
  };

  // 場所選択
  public onSelectBsy = (item?: any) => {
    const useKnoFlgSteBsyHenTem = this.props.dCEF0020OutState.root.mesKmk.inoKmk.useHyjKnoJho.useKnoFlgSteBsyHen;
    if (useKnoFlgSteBsyHenTem === '0') {
      return;
    }
    if (!this.state.heightAuto) {
      this.setState({
        heightAuto: true,
      });
    } else {
      this.setState({
        heightAuto: false,
        httKnoScnSteBsyJhoKno: item,
      });
    }
  };

  // 設定完了ボタンを押下
  public finished = () => {
    // 画面入力項目チェック
    if (Tools.isSubmit() && this.validatCheck()) {
      // 設定完了画面へ遷移するパラメータ初期化
      const initeParams = {
        sinCd: this.myState.sinCd,
        kkkCdKm: this.myState.kkkCdKm,
        accTok: this.myState.accTok,
        sonId: this.myState.sonId,
        son2Id: this.myState.son2Id,
        sniMtoStmId: this.myState.sniMtoStmId,
        sniMtoKinId: this.myState.sniMtoKinId,
        nnsSitMlAdr: this.myState.nnsSitMlAdr,
        nnsSitMlPcKetKbnCd: this.myState.nnsSitMlPcKetKbnCd,
        nmtId: this.myState.nmtId,
        nisHntCd: this.myState.nisHntCd,
        sniMtoGmId: 'DCWBEFRC1030',
        dnpNo: this.myState.dnpNo,
      };
      // DCEF0060 API アクセス
      const dCEF0060InTypesTem = this.initDCEF0060InTypes;
      if (this.state.mailAdrInput !== '') {
        dCEF0060InTypesTem.root.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdr = this.state.mailAdrInput;
      } else {
        dCEF0060InTypesTem.root.mesKmk.inoKmk.iriUsrJho.iriKryMlKboKbn = '1';
      }
      if (this.state.iriKryMlKboKbn === '1') {
        dCEF0060InTypesTem.root.mesKmk.inoKmk.iriUsrJho.iriKryMlKboKbn = '1';
      }
      dCEF0060InTypesTem.root.mesKmk.inoKmk.nmtMesJho.scnHttKboHdk = this.state.httDate;
      dCEF0060InTypesTem.root.mesKmk.inoKmk.nmtMesJho.scnHttKboBsyCd = this.state.httKnoScnSteBsyJhoKno.scnHttbsyCd;
      this.props.getDcef0060List(dCEF0060InTypesTem, () => {
        Tools.isSubmitFlg = true;
        this.props.history.push({ pathname: '/ec/complete', state: initeParams });
      });
    }
  };

  // 画面入力項目チェック
  private validatCheck = () => {
    const { iriUsrJho } = this.props.dCEF0020OutState.root.mesKmk.inoKmk;
    // メール入力可能時
    if (iriUsrJho.nnsSitMlAdrInpKnoFlg === '1') {
      // 「確認用メールアドレス」入力チェック
      if (this.state.mailAdrInput && this.state.mailAdrComfirm === '') {
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_COMFIRM_ERR]);
        return false;
      }
      // 「変更受付通知先メールアドレス」入力チェック
      if (this.state.mailAdrInput === '' && this.state.mailAdrComfirm) {
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_INPUT_ERR]);
        return false;
      }
      // 「確認用メールアドレス」と「変更受付通知先メールアドレス」の一致性チェック
      if (this.state.mailAdrInput !== this.state.mailAdrComfirm) {
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_DIFFRENT_ERR]);
        return false;
      }
    }
    // メールアドレス必須入力チェック
    if (iriUsrJho.EmlHsuFlg === '1') {
      if (
        (iriUsrJho.nnsSitMlAdrInpKnoFlg === '1' && this.state.mailAdrInput === '') ||
        (iriUsrJho.nnsSitMlAdr !== '' && !this.state.iriKryMlKboChkbx)
      ) {
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_MUST_ERR]);
        return false;
      }
    }

    return true;
  };

  // 変更完了通知先メールアドレス フォーマットチェック
  public mailAdrCheck = (e: any) => {
    const mailAdr = e.target.value;
    if (mailAdr !== '') {
      if (!Validator.email(mailAdr)) {
        if (this.state.completeIsDisable) {
          this.setState({
            completeIsDisable: false,
          });
        }
        this.props.dispCurrentErrors([ErrorMsgs.MAIL_INVALID_ERR]);
      } else {
        this.props.dispCurrentErrors([]);
        this.setState({ mailAdrInput: mailAdr, completeIsDisable: true });
      }
    } else {
      this.props.dispCurrentErrors([]);
      if (this.state.completeIsDisable) {
        this.setState({
          completeIsDisable: false,
        });
      }
    }
  };

  public render() {
    const { inoKmk } = this.props.dCEF0020OutState.root.mesKmk;
    return (
      <main className={`content change-drop-service-${this.state.dispKbn === 0 ? 'date' : 'place'}`}>
        <ErrorArea errors={this.props.errors} />
        <div className="content-inner">
          <div className="page-header">
            <h1 className="page-title">敷地内配達の日付／指定場所の変更</h1>
          </div>
          <div className="drop-service-steps">
            <div className={`drop-service-step ${this.state.dispKbn === 0 && 'is-current'}`}>1.日付変更</div>
            <div className={`drop-service-step ${this.state.dispKbn !== 0 && 'is-current'}`}>2.指定場所変更</div>
            <div className="drop-service-step">3.完了</div>
          </div>
          {this.state.dispKbn === 0 && ( // 日付変更
            <div className="drop-service-blocks">
              <form>
                <div className="drop-service-block">
                  <div className="drop-service-block-title">{this.state.dispKbn === 0 ? 'ご希望日' : 'ご希望場所'}</div>
                  <div className="drop-service-dates">
                    {inoKmk.useHyjKnoJho.useKnoFlgScnHttHdkHen === '0' &&
                      inoKmk.httKboScnHttHdkJhoKnoLst.map((item) => {
                        return (
                          item.scnHttHdkHenMaeStkFlg === '1' && (
                            <div key={item.scnHttHdk} className="drop-service-date-current">
                              <input type="radio" name="select-date" id="select-date-current" defaultChecked />
                              <label htmlFor="select-date-current">{item.scnHttHdkNm}</label>
                            </div>
                          )
                        );
                      })}
                    {inoKmk.useHyjKnoJho.useKnoFlgScnHttHdkHen !== '0' &&
                      inoKmk.httKboScnHttHdkJhoKnoLst.map((item, index) => {
                        return (
                          index === 0 && (
                            <div key={item.scnHttHdk} className="drop-service-date-current" onClick={this.changeDate.bind(this, item)}>
                              {item.scnHttHdk === this.state.httDate ? (
                                <input type="radio" name="select-date" id="select-date-current" defaultChecked />
                              ) : (
                                <input type="radio" name="select-date" id="select-date-current" />
                              )}
                              <label htmlFor="select-date-current">{item.scnHttHdkNm}</label>
                            </div>
                          )
                        );
                      })}
                    {inoKmk.useHyjKnoJho.useKnoFlgScnHttHdkHen !== '0' && (
                      <div className="drop-service-date-list">
                        {inoKmk.httKboScnHttHdkJhoKnoLst.map((item, index) => {
                          return (
                            index > 0 && (
                              <div key={item.scnHttHdk} className="drop-service-date-item" onClick={this.changeDate.bind(this, item)}>
                                {item.scnHttHdk === this.state.httDate ? (
                                  <input type="radio" name="select-date" id={`date-${item.scnHttHdk}`} defaultChecked />
                                ) : (
                                  <input type="radio" name="select-date" id={`date-${item.scnHttHdk}`} />
                                )}
                                <label htmlFor={`date-${item.scnHttHdk}`}>{item.scnHttHdkNm}</label>
                              </div>
                            )
                          );
                        })}
                      </div>
                    )}
                  </div>
                </div>
                <div className="drop-service-block-button">
                  <button type="button" onClick={this.dispChange.bind(this, 1)}>
                    指定場所の変更へ
                  </button>
                </div>
              </form>
            </div>
          )}
          {this.state.dispKbn !== 0 && ( // 場所変更
            <div className="drop-service-blocks">
              <form>
                <div className="drop-service-block">
                  <div className="drop-service-block-title">ご希望場所</div>
                  <div className="drop-service-dropdown">
                    <div
                      className={`drop-service-dropdown-selected ${inoKmk.useHyjKnoJho.useKnoFlgSteBsyHen === '0' ? 'is-disabled' : ''}`}
                      onClick={this.onSelectBsy.bind(this, {})}
                    >
                      {this.state.httKnoScnSteBsyJhoKno.scnHttbsyNm}
                    </div>
                    <div
                      className="dropdown-clone js-dropdown-clone"
                      style={this.state.heightAuto ? { height: `auto`, display: `block` } : { display: `none` }}
                    >
                      {inoKmk.httKnoScnSteBsyJhoKnoLst.map((item) => {
                        return (
                          <div key={item.scnHttbsyCd} className="clone-item" onClick={this.onSelectBsy.bind(this, item)}>
                            {item.scnHttbsyNm}
                          </div>
                        );
                      })}
                    </div>
                    {this.state.heightAuto && <div className="drop-service-dropdown-background" onClick={this.closePulldown} />}
                  </div>
                </div>
                {this.props.dCEF0020OutState.root.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg === '1' && (
                  <div className="drop-service-block">
                    <div className="drop-service-block-title">
                      変更受付通知先メールアドレス<span>（半角英数字）</span>
                    </div>
                    <p>{ParamsFiles.MAIL_CAREFULLY_TEXT1}</p>
                    <div className="drop-service-email-setting">
                      <input
                        type="text"
                        onBlur={(e) => this.mailAdrCheck(e)}
                        onChange={(e) => this.inputChange(e, 'mailAdrInput')}
                        value={this.state.mailAdrInput}
                      />
                    </div>
                  </div>
                )}
                {this.props.dCEF0020OutState.root.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdrInpKnoFlg === '1' && (
                  <div className="drop-service-block">
                    <div className="drop-service-block-title">
                      確認用メールアドレス<span>（半角英数字）</span>
                    </div>
                    <div className="drop-service-email-setting">
                      <input type="text" onChange={(e) => this.inputChange(e, 'mailAdrComfirm')} value={this.state.mailAdrComfirm} />
                    </div>
                  </div>
                )}
                {this.props.dCEF0020OutState.root.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdr !== '' && (
                  <div className="drop-service-block">
                    <div className="drop-service-block-title">完了メールの送付</div>
                    <p>{ParamsFiles.MAIL_CAREFULLY_TEXT0}</p>
                    <div className="drop-service-email">
                      <input
                        type="checkbox"
                        name="send-complete-mail"
                        id="send-complete-mail"
                        onChange={(e) => this.inputChange(e, 'iriKryMlKboChkbx')}
                        defaultChecked={this.state.iriKryMlKboChkbx}
                        disabled={this.props.dCEF0020OutState.root.mesKmk.inoKmk.iriUsrJho.EmlHsuFlg === '1'}
                      />
                      <label htmlFor="send-complete-mail">{this.props.dCEF0020OutState.root.mesKmk.inoKmk.iriUsrJho.nnsSitMlAdr}</label>
                    </div>
                  </div>
                )}
                <div className="drop-service-block-note">
                  <p>{ParamsFiles.HENKO_IRAI_CAREFULLY_TEXT}</p>
                </div>
                <div className={`drop-service-block-button${this.state.completeIsDisable ? '' : ' is-disabled'}`}>
                  <button type="button" onClick={this.finished}>
                    設定完了
                  </button>
                </div>
              </form>
            </div>
          )}
          {this.state.dispKbn !== 0 && (
            <div className="return-box">
              <a className="return-button" onClick={this.dispChange.bind(this, 0)}>
                日付変更へ戻る
              </a>
            </div>
          )}
          <div className="return-box">
            <a className="return-button" onClick={this.gotoTop.bind(true)}>
              TOPページへ戻る
            </a>
          </div>
        </div>
      </main>
    );
  }
}

// stateとpropsを関連
const mapStateToProps = ({ dCEF0020States, dCEF0060States, errorsState }: IApplicationState) => {
  return {
    errors: errorsState.errors,
    dCEF0020OutState: dCEF0020States.dCEF0020OutType,
    dCEF0060OutState: dCEF0060States.dCEF0060OutType,
  };
};

// dispatchとコンポーネントpropsを関連
const mapDispatchToProps = (dispatch: Dispatch) => ({
  getDcef0020List: bindActionCreators(DCEF0020Actions.fetchDcef0020List, dispatch),
  getDcef0060List: bindActionCreators(DCEF0060Actions.fetchDcef0060List, dispatch),
  dispCurrentErrors: bindActionCreators(ErrorActions.dispCurrentErrors, dispatch),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DCWBEFRC1030));
