import Constants from '../common/constants';
/**
 * 共通ツール
 *
 * @export
 * @class Tools
 */
const Tools = {
  // 端末判断
  isSp: () => {
    const sUserAgent = navigator.userAgent.toLowerCase();
    const bIsIpad = sUserAgent.match(/ipad/i);
    const bIsMidp = sUserAgent.match(/midp/i);
    const bIsIphoneOs = sUserAgent.match(/iphone os/i);
    const bIsAndroid = sUserAgent.match(/android/i);
    const bIsWinCe = sUserAgent.match(/windows ce/i);
    const bIsWinMob = sUserAgent.match(/windows mobile/i);

    if (bIsIpad || bIsMidp || bIsIphoneOs || bIsAndroid || bIsWinCe || bIsWinMob) {
      return true;
    }
    return false;
  },

  // 二重送信判定フラグ
  isSubmitFlg: true,
  // 二重送信判定
  isSubmit: () => {
    // クリックできない
    if (!Tools.isSubmitFlg) {
      return false;
    }

    // できる
    Tools.isSubmitFlg = false;
    return true;
  },

  // URLにパラメータ取得
  getParams: (myState: any, callback: any) => {
    const { href } = window.location;
    const hrefParams = href.split('?')[1];
    const gtState = myState;
    if (hrefParams) {
      const hrefArr = hrefParams.split('&');
      hrefArr.map((item) => {
        const itemArr = item.split('=');
        const [key, val] = itemArr;
        gtState[key] = val;
        return '';
      });
    }
    callback(gtState);
  },

  // 外部サイト判断
  isIntraSite: () => {
    const { referrer } = document;
    if (referrer === '') {
      return true;
    }
    let isIntra = false;
    Constants.EC_ROUTERS.map((item) => {
      if (referrer.includes(item)) {
        isIntra = true;
      }
      return '';
    });
    return isIntra;
  },

  /**
   * cookieを取得
   *
   * @param {string} name
   * @returns
   */
  getCookie: (name: string) => {
    const reg = new RegExp(`(^| )${name}=([^;]*)(;|$)`);
    const arr = document.cookie.match(reg);
    if (arr) {
      return unescape(arr[2]);
    }
    return null;
  },
};

export default Tools;
