import { Dispatch } from 'redux';
import { push } from 'connected-react-router';
import { DCEF0040ActionTypes } from '../models/DCEF0040.model';
import { commonActionTypes } from '../models/common.model';
import { OshiraseErrorsCode, OtherSysErrorsCode, CurrentErrorsCode, NormalErrorsCode } from '../../common/message';

import DCEF0040Api from '../api/DCEF0040.api';

const DCEF0040Actions = {
  fetchDcef0040List(params: any, callback?: any) {
    return (dispatch: Dispatch) => {
      dispatch({ type: DCEF0040ActionTypes.FETCH_API_DCEF0040 });

      DCEF0040Api.fetchDCEF0040List(params).then((res: any) => {
        const resultTem = res.data;
        if (resultTem && (resultTem.root || NormalErrorsCode.includes(resultTem.errors[0].code))) {
          if (callback) {
            callback(resultTem);
          }
          dispatch({ type: commonActionTypes.ERROR_CLEAR });
          dispatch({ type: DCEF0040ActionTypes.FETCH_API_DCEF0040_SUCCESS, payload: res.root });
        } else if (resultTem.errors) {
          const firstError = resultTem.errors[0];
          // お知らせ画面へ遷移するエラー
          if (OshiraseErrorsCode.includes(firstError.code)) {
            const initParam = {
              field: firstError.field,
              code: firstError.code,
              message: firstError.message,
              dnpNo: params.root.mesKmk.nmtJho.dnpNo,
            };
            dispatch(push({ pathname: '/ec/osirase', state: initParam }));
          }
          // 他のシステムへ遷移するエラー
          else if (OtherSysErrorsCode.includes(firstError.code)) {
            // callback();
          }
          // 自画面表示するエラー
          else if (CurrentErrorsCode.includes(firstError.code)) {
            dispatch({ type: commonActionTypes.API_ERROR, payload: resultTem.errors });
          }
          // 正常に扱い
          else if (NormalErrorsCode.includes(firstError.code)) {
            // 何でもない
          }
          dispatch({
            type: commonActionTypes.API_ERROR,
            payload: resultTem.errors,
          });
        }
      });
    };
  },
};

export default DCEF0040Actions;
