import 'react-app-polyfill/ie11';
import 'react-app-polyfill/stable';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { createBrowserHistory } from 'history';
// import { createHashHistory } from 'history';

import configureStore from './configureStore';
import App from './App';

import * as serviceWorker from './serviceWorker';

// browser history作成
// const history = createHashHistory();
const history = createBrowserHistory();
// const history = supportsHistory() ? createBrowserHistory() : createHashHistory();
// windowから初期のstateを取得する
const initialState = (window as any).initialReduxState;
// store初期化
const { store, persistor } = configureStore(history, initialState);

// ルートコンポーネントをレンダリングして，storeとhistory属性を渡し
ReactDOM.render(<App store={store} persistor={persistor} history={history} />, document.getElementById('root') as HTMLElement);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
