class CookieStorage {
  cookies: any;

  keyPrefix: string;

  indexKey: string;

  expiration: any;

  setCookieOptions: any;

  secure: boolean;

  domain: string;

  constructor(cookies: any, options: any = {}) {
    this.cookies = cookies;
    this.keyPrefix = options.keyPrefix || '';
    this.indexKey = options.indexKey || 'reduxPersistIndex';
    this.expiration = options.expiration || {};
    this.secure = options.secure || false;
    this.domain = options.domain || '';
    if (!this.expiration.default) {
      this.expiration.default = null;
    }
    this.setCookieOptions = options.setCookieOptions;
  }

  getItem(key: string, callback?: Function): Promise<any> {
    const item = this.cookies.get(this.keyPrefix + key) || null;
    if (callback) {
      callback(null, item);
    }
    return Promise.resolve(item);
  }

  async setItem(key: string, value: string, callback?: Function): Promise<any> {
    const options = { ...this.setCookieOptions };
    let expires = this.expiration.default;
    if (typeof this.expiration[key] !== 'undefined') {
      expires = this.expiration[key];
    }
    if (expires) {
      options.expires = expires;
    }
    if (this.secure) {
      options.secure = this.secure;
    }
    if (this.domain) {
      options.domain = this.domain;
    }
    this.cookies.set(this.keyPrefix + key, value, options);
    // Update key index
    const indexOptions = { ...this.setCookieOptions };
    if (this.expiration.default) {
      indexOptions.expires = this.expiration.default;
    }
    const allKeys = await this.getAllKeys();
    if (allKeys.indexOf(key) === -1) {
      allKeys.push(key);
      this.cookies.set(this.indexKey, JSON.stringify(allKeys), indexOptions);
    }
    if (callback) {
      callback(null);
    }
    return Promise.resolve(null);
  }

  async removeItem(key: string, callback?: Function): Promise<any> {
    this.cookies.expire(this.keyPrefix + key);
    const allKeys = await this.getAllKeys();
    const filterKeys = allKeys.filter((k: any) => {
      return k !== key;
    });
    this.cookies.set(this.indexKey, JSON.stringify(filterKeys));
    if (callback) {
      callback(null);
    }
    return Promise.resolve(null);
  }

  getAllKeys(callback?: Function): Promise<any> {
    const cookie = this.cookies.get(this.indexKey);
    let result = [];
    if (cookie) {
      result = JSON.parse(cookie);
    }
    if (callback) {
      callback(null, result);
    }
    return Promise.resolve(result);
  }
}

export default CookieStorage;
