import { Reducer } from 'redux';
import { DCEF1200ActionTypes, IDCEF1200States } from '../models/DCEF1200.model';

// state初期化
export const initDCEF1200State: IDCEF1200States = {
  dCEF1200OutType: {
    result: {
      mesKmk: {
        inoKmk: {
          httKbohttBsyJho: {
            uktKnoTs: '',
            hokKjnTs: '',
          },
          iriUsrJho: {
            nnsSitMlAdr: '',
            iriKryMlKboKbn: '',
          },
          nmtMesJho: {
            httJky: '',
            Okn: '',
            GmnHyjNmtSiz: '',
            GmnHyjOtdTs: '',
            GmnHyjUktBsy: '',
            GmnHyjSasHenD: '',
            GmnHyjUktKnoTs: '',
            GmnHyjUktKgnD: '',
          },
        },
      },
    },
  },
};

// DCEF1200 reducer
const DCEF1200Reducer: Reducer<IDCEF1200States> = (state = initDCEF1200State, action) => {
  switch (action.type) {
    // DCEF1200
    case DCEF1200ActionTypes.FETCH_API_DCEF1200: {
      return { ...state };
    }
    case DCEF1200ActionTypes.FETCH_API_DCEF1200_SUCCESS: {
      return {
        ...state,
        dCEF1200OutType: action.payload,
      };
    }
    default: {
      return state;
    }
  }
};

export { DCEF1200Reducer };
