import DEV01_EXTRA_ENVS from '../env/dev01-extra.env.properties';
import DEV01_INTRA_ENVS from '../env/dev01-intra.env.properties';
import HONBAN_EXTRA_ENVS from '../env/honban-extra.env.properties';
import HONBAN_INTRA_ENVS from '../env/honban-intra.env.properties';
import DEV_TEST_EXTRA_ENVS from '../env/dev-test.env.properties';
import Constants from '../common/constants';

const envUtil = {
  analysisEnv: () => {
    const nodeenv = process.env.NODE_ENV;
    const { origin } = window.location;
    let ENV_PARAM = DEV_TEST_EXTRA_ENVS;

    if (nodeenv === 'production') {
      if (origin.includes(Constants.DOMAINS.DEV01_EXTRA_DOMAIN)) {
        ENV_PARAM = DEV01_EXTRA_ENVS;
      } else if (origin.includes(Constants.DOMAINS.DEV01_INTRA_DOMAIN)) {
        ENV_PARAM = DEV01_INTRA_ENVS;
      } else if (origin.includes(Constants.DOMAINS.HONBAN_EXTRA_DOMAIN)) {
        ENV_PARAM = HONBAN_EXTRA_ENVS;
      } else if (origin.includes(Constants.DOMAINS.HONBAN_INTRA_DOMAIN)) {
        ENV_PARAM = HONBAN_INTRA_ENVS;
      }
    }
    return ENV_PARAM;
  },

  getEnv: (name: string) => {
    const ENV_PARAM = envUtil.analysisEnv();
    let val = '';
    Object.keys(ENV_PARAM).map((key) => {
      if (name === key) {
        val = Object(ENV_PARAM)[key];
      }
      return '';
    });
    return val;
  },
};

export default envUtil;
