import React from 'react';
import { connect } from 'react-redux';
import { Dispatch, bindActionCreators } from 'redux';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import monment from 'moment';

import Validator from '../../../../utils/validator';
import Tools from '../../../../utils/commonUtil';
import { IApplicationState } from '../../../../redux/store';
import DCEF1300Actions from '../../../../redux/modules/DCEF1300.actions';
import DCEF1180Actions from '../../../../redux/modules/DCEF1180.actions';
import { IDCEF1300OutState } from '../../../../redux/models/DCEF1300.model';
import { IDCEF1180OutState } from '../../../../redux/models/DCEF1180.model';
import { IError } from '../../../../redux/models/common.model';
import ErrorArea from '../../components/layout/errorsArea/ErrorArea.component';
import ParamsFiles from '../../../../common/paramsFile';
import ErrorActions from '../../../../redux/modules/error.actions';
import { ErrorMsgs } from '../../../../common/message';
import CommonActions from '../../../../redux/modules/common.actions';

/**
 * 画面内部用
 *
 * @interface PropsFromState
 */
interface IPropsFromState {
  errors: IError[];
  dCEF1300OutState: IDCEF1300OutState;
  dCEF1180OutState: IDCEF1180OutState;
}

/**
 * 画面用action
 *
 * @interface PropsFromDispatch
 */
interface IPropsFromDispatch {
  getDcef1300List: typeof DCEF1300Actions.fetchDcef1300List;
  getDcef1180List: typeof DCEF1180Actions.fetchDcef1180List;
  dispCurrentErrors: typeof ErrorActions.dispCurrentErrors;
  extraParamsMustNg: typeof CommonActions.extraParamsMustNg;
}
/**
 * 画面用state
 *
 * @interface StateOwn
 */
interface IStateOwn {
  telNo: string;
  isDisabled: boolean;
  [key: string]: any;
}
// エンテイを統合
type AllProps = IPropsFromState & IPropsFromDispatch & RouteComponentProps;

class DCWBEFRC1010 extends React.Component<AllProps, IStateOwn> {
  public myState = Object(this.props.history.location.state);

  // DCEF1300APIパラメータ初期化
  public initDCEF1300InTypes = {
    root: {
      ktuJho: {
        iriJho: {
          iriTs: monment().format('YYYYMMDDHHmmss'),
          tsyCd: '',
          sinCd: '',
          kkkCdKm: '',
          serIpAdr: '',
          tnmIpAdr: '',
        },
        nisJho: {
          kisId: '12',
          sonId: '',
          tnmSbtCd: '',
        },
      },
      mesKmk: {
        nmtJho: {
          dnpNo: this.myState.dnpNo ? this.myState.dnpNo : this.myState.denpyoCd,
        },
      },
    },
  };

  // DCEF1180APIパラメータ初期化
  public initDCEF1180InTypes = {
    root: {
      ktuJho: {
        iriJho: {
          iriTs: monment().format('YYYYMMDDHHmmss'),
          tsyCd: '',
          sinCd: '',
          kkkCdKm: '',
          serIpAdr: '',
          tnmIpAdr: '',
        },
        nisJho: {
          kisId: '12',
          sonId: '',
          tnmSbtCd: '',
        },
      },
      mesKmk: {
        nmtJho: {
          dnpNo: this.myState.dnpNo ? this.myState.dnpNo : this.myState.denpyoCd,
        },
        inoKmk: {
          iriUsrJho: {
            tel: '',
          },
        },
      },
    },
  };

  constructor(props: AllProps) {
    super(props);
    this.state = {
      telNo: '',
      isDisabled: true,
    };
  }

  public componentDidMount() {
    // 外部サイトからGET遷移時、パラメータ取得
    Tools.getParams(this.myState, (gtState: any) => {
      this.myState = gtState;
    });

    // 外部サイトから遷移時、パラメータを必要チェック
    if (!this.props.history.location.state && !this.intraParamCheck()) {
      this.props.extraParamsMustNg();
    }

    // タイトル設定
    document.title = ParamsFiles.DCWBEFRC1010_TXT.TITLE;
    this.initPostParams();
    const dCEF1300InTypes = this.initDCEF1300InTypes;
    dCEF1300InTypes.root.ktuJho.nisJho.tnmSbtCd = Tools.isSp() ? '3' : '1';
    this.props.getDcef1300List(dCEF1300InTypes, () => {
      // 「荷物お問い合わせ」のサイトへリダイレクトする
      const redirectForm = document.createElement('form');
      redirectForm.action = ParamsFiles.DCWBEFRC1010_TXT.RECEIVE_LNEKO_URL;
      document.body.appendChild(redirectForm);
      const userid = document.createElement('input');
      userid.type = 'hidden';
      userid.name = 'userid';
      userid.value = ParamsFiles.DCWBEFRC1010_TXT.USER_ID;

      const number00 = document.createElement('input');
      number00.type = 'hidden';
      number00.name = 'userid';
      number00.value = '1';

      const number01 = document.createElement('input');
      number01.type = 'hidden';
      number01.name = 'userid';
      number01.value = this.myState.dnpNo;

      redirectForm.appendChild(userid);
      redirectForm.appendChild(number00);
      redirectForm.appendChild(number01);
      redirectForm.method = 'POST';
      redirectForm.submit();
      document.body.removeChild(redirectForm);
    });
  }

  // 画面間の引継ぎ項目を再設定
  public initPostParams = () => {
    this.myState.sniMtoStmId = this.myState.sniMtoStmId ? this.myState.sniMtoStmId : this.myState.ninushiId;
    this.myState.sniMtoKinId = this.myState.sniMtoKinId ? this.myState.sniMtoKinId : this.myState.functionId;
    this.myState.nnsSitMlAdr = this.myState.nnsSitMlAdr ? this.myState.nnsSitMlAdr : this.myState.emailAdress;
    this.myState.nnsSitMlPcKetKbnCd = this.myState.nnsSitMlPcKetKbnCd ? this.myState.nnsSitMlPcKetKbnCd : this.myState.emailCd;
    this.myState.dnpNo = this.myState.dnpNo ? this.myState.dnpNo : this.myState.denpyoCd;
  };

  // パラメータチェック
  public intraParamCheck = () => {
    if (this.myState.ninushiId && this.myState.denpyoCd) {
      return true;
    }
    return false;
  };

  // 電話番号チェック
  public telNoCheck = (e: any) => {
    const tel = e.target.value;
    if (tel === '') {
      this.props.dispCurrentErrors([]);
      this.setState({ isDisabled: true });
    } else if (!Validator.keta11(tel)) {
      this.props.dispCurrentErrors([ErrorMsgs.TEL_BYTE_ERR]);
      this.setState({ telNo: '', isDisabled: true });
    } else if (!Validator.digit(tel)) {
      this.props.dispCurrentErrors([ErrorMsgs.TEL_INVALID_ERR]);
      this.setState({ telNo: '', isDisabled: true });
    } else {
      this.props.dispCurrentErrors([]);
      // 電話番号state設定
      this.setState({ telNo: tel, isDisabled: false });
    }
  };

  // 送信する ボタンを押下
  public send = () => {
    const tel = this.state.telNo;
    if (tel === '') {
      this.props.dispCurrentErrors([ErrorMsgs.TEL_MUST_ERR]);
    } else if (Tools.isSubmit()) {
      const dCEF1180InTypes = this.initDCEF1180InTypes;
      dCEF1180InTypes.root.ktuJho.nisJho.tnmSbtCd = Tools.isSp() ? '3' : '1';
      dCEF1180InTypes.root.mesKmk.inoKmk.iriUsrJho.tel = this.state.telNo;
      this.props.getDcef1180List(dCEF1180InTypes, () => {
        const initeParams = {
          sniMtoStmId: this.myState.sniMtoStmId,
          sniMtoKinId: this.myState.sniMtoKinId,
          nnsSitMlAdr: this.myState.nnsSitMlAdr ? this.myState.nnsSitMlAdr : '',
          nnsSitMlPcKetKbnCd: this.myState.nnsSitMlPcKetKbnCd ? this.myState.nnsSitMlPcKetKbnCd : '',
          dnpNo: this.myState.dnpNo,
        };
        Tools.isSubmitFlg = true;
        this.props.history.push({ pathname: '/ec/top', state: initeParams });
      });
    }
  };

  // Enterキー禁止
  forbidEnter = (e: any) => {
    e.preventDefault();
  };

  public render() {
    return (
      <main className="content tel-certification">
        <ErrorArea errors={this.props.errors} />
        <div className="content-inner">
          <div className={`certification-box ${this.state.isDisabled ? 'is-disabled' : ''}`}>
            <form onSubmit={(e) => this.forbidEnter(e)}>
              <div className="certification-lead">{ParamsFiles.DCWBEFRC1010_TXT.TEL_NO_LABEL_TEXT}</div>
              <input type="text" placeholder={ParamsFiles.DCWBEFRC1010_TXT.TEL_NO_INPUT_TEXT} onBlur={(e) => this.telNoCheck(e)} />
              <button type="button" onClick={this.send}>
                送信する
              </button>
            </form>
          </div>
        </div>
      </main>
    );
  }
}

// stateとpropsを関連
const mapStateToProps = ({ dCEF1180States, dCEF1300States, errorsState }: IApplicationState) => {
  return {
    errors: errorsState.errors,
    dCEF1180OutState: dCEF1180States.dCEF1180OutType,
    dCEF1300OutState: dCEF1300States.dCEF1300OutType,
  };
};

// dispatchとコンポーネントpropsを関連
const mapDispatchToProps = (dispatch: Dispatch) => ({
  getDcef1180List: bindActionCreators(DCEF1180Actions.fetchDcef1180List, dispatch),
  getDcef1300List: bindActionCreators(DCEF1300Actions.fetchDcef1300List, dispatch),
  dispCurrentErrors: bindActionCreators(ErrorActions.dispCurrentErrors, dispatch),
  extraParamsMustNg: bindActionCreators(CommonActions.extraParamsMustNg, dispatch),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DCWBEFRC1010));
