import { Reducer } from 'redux';
import { DCEF0020ActionTypes, IDCEF0020States } from '../models/DCEF0020.model';

// state初期化
export const initDCEF0020State: IDCEF0020States = {
  dCEF0020OutType: {
    root: {
      mesKmk: {
        inoKmk: {
          useHyjKnoJho: {
            useKnoFlgScnHttHdkHen: '',
            useKnoFlgSteBsyHen: '',
            useKnoFlgScnHtt: '',
          },
          httKboScnHttHdkJhoKnoLst: [],
          httKnoScnSteBsyJhoKnoLst: [],
          iriUsrJho: {
            nnsSitMlAdrInpKnoFlg: '',
            nnsSitMlAdr: '',
            nnsSitMlPcKetKbnCd: '',
            EmlHsuFlg: '',
            iriKryMlKboKbn: '',
          },
        },
      },
    },
  },
};

// DCEF0020 reducer
const DCEF0020Reducer: Reducer<IDCEF0020States> = (state = initDCEF0020State, action) => {
  switch (action.type) {
    // DCEF0020
    case DCEF0020ActionTypes.FETCH_API_DCEF0020: {
      return { ...state };
    }
    case DCEF0020ActionTypes.FETCH_API_DCEF0020_SUCCESS: {
      return {
        ...state,
        dCEF0020OutType: action.payload,
      };
    }
    default: {
      return state;
    }
  }
};

export { DCEF0020Reducer };
