import { Reducer } from 'redux';
import { DCEF1180ActionTypes, IDCEF1180States } from '../models/DCEF1180.model';

// state初期化
export const initDCEF1180State: IDCEF1180States = {
  dCEF1180OutType: {
    result: {
      mesKmk: {
        nmtJho: {
          nmtId: '',
        },
      },
    },
  },
};

// DCEF1180 reducer
const DCEF1180Reducer: Reducer<IDCEF1180States> = (state = initDCEF1180State, action) => {
  switch (action.type) {
    // DCEF1180
    case DCEF1180ActionTypes.FETCH_API_DCEF1180: {
      return { ...state };
    }
    case DCEF1180ActionTypes.FETCH_API_DCEF1180_SUCCESS: {
      return {
        ...state,
        dCEF1180OutType: action.payload,
      };
    }
    default: {
      return state;
    }
  }
};

export { DCEF1180Reducer };
